html, body, #root {
    width: 100%;
    height: 100%;
}
html, body, #root, button, button, input, select, option {
    font-family: 'Open Sans', sans-serif;
    font-size: inherit;
}
* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: inherit;
}

ul, ol {
    list-style: none;
}

button {
    border: 0 none;
    background: none;
    padding: 0;
}

b {
    font-weight: bold;
}

.root__loading {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    min-height: 100vh;
}

.screen {
    position: relative;
    min-height: 100%;
    background: #f5f5f5;
}

.inner {
    padding: 10px;
}

.btn_warning {
    color: #ffffff;
    height: 35px;
    width: 100%;
    background-color: #cb2431;
    font-weight: bold;
}

.warning {
    background-color: rgba(203,36,49,.8);
    display: block;
    color: #fff;
    font-size: 14px;
}



